.AdminPage {
  max-width: 100%;
  overflow-y: hidden;
  .add-form {
    background: #f9f9f9;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    width: 400px; 
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); 
    display: flex;
    flex-direction: column; 
    position: fixed; 
    top: 50%; 
    left: 50%; 
    transform: translate(-50%, -50%); 
    z-index: 9999; 
  }

  
  .add-form h3 {
    margin-bottom: 15px;
    text-align: center; 
    font-size: 18px;
    font-weight: bold;
    color: #333; 
  }

  
  .add-form div {
    margin-bottom: 15px; 
  }

  .add-form label {
    display: block;
    font-weight: bold;
    font-size: 14px;
    color: #333; 
  }

  .add-form input,
  .add-form select {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
    color: #333; 
  }

  .add-form input:focus,
  .add-form select:focus {
    border-color: #007bff; 
    outline: none;
  }

  
  .add-form button {
    padding: 12px 20px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease; 
  }

  .add-form button:hover {
    background: #0056b3;
  }

  
  .Admin-right {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-bottom: 20px;
    .title {
      display: flex;
      padding: 20px 50px;
      justify-content: space-between;
      align-items: center;
      background-color: antiquewhite;
      height: 100px;

      h5 {
        left: 0;
        cursor: pointer;
        font-size: 16px;
        font-weight: bold;
        color: #333;
      }

      h5:hover {
        color: #007bff;
      }
    }
    .btn {
      display: flex;
      gap: 30px;
      justify-content: center;

      button {
        height: 35px;
        width: 120px;
        border-radius: 10px;
        background-color: rgb(145, 205, 205);
        font-size: 14px;
        font-weight: bold;
        transition: background-color 0.3s ease; 
      }

      button:hover {
        color: #fff;
        background-color: burlywood;
      }
    }
  }
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }

  .modal {
    border-radius: 8px;
    width: 450px;
    display: flex;
    flex-direction: column-reverse;
  }

  .modal-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }

  .modal-buttons button {
    padding: 12px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
  }

  .modal-buttons button:first-child {
    background-color: #28a745;
    color: white;
  }

  .modal-buttons button:last-child {
    background-color: #dc3545;
    color: white;
  }
  .gohome {
    padding: 20px 20px;
   a{
    border-radius: 15px;
    border: 1px solid #000000;
    padding: 20px;
   }
   a:hover {
    background-color: #0056b3;
   }
  }
  @keyframes slideDown {
    from {
      transform: translateY(-50px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
