.filters {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    gap: 20px;
  
    .filter-item {
      display: flex;
      align-items: center;
  
      label {
        font-size: 14px;
        font-weight: 600;
        margin-right: 10px;
        color: #333;
      }
  
      select,
      input[type="text"] {
        padding: 12px 16px;
        border: 1px solid #ccc;
        border-radius: 8px;
        font-size: 16px;
        outline: none;
        width: 250px;
        transition: all 0.3s ease;
  
        &:focus {
          border-color: #007bff;
          box-shadow: 0 0 8px rgba(0, 123, 255, 0.5);
        }
      }
  
      input[type="text"] {
        max-width: 400px;
      }
    }
  }
  